  .loader1:before,
  .loader1:after {
    content: " ";
    display: table-cell;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    position: absolute;
    animation-duration: .8s;
    animation-name: revolve;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    mix-blend-mode: darken;
  }
