  .loader1:before {
    background: rgb(77, 232, 244);
  }
  
  .loader1:after {
    background: rgb(253, 62, 62);
    animation-delay: 0.4s;
  }
  
  @keyframes revolve {
    0% {
      left: 0;
    }
    25% {
      transform: scale(calc(1 + 0.2));
    }
    50% {
      left: 50px;
    }
    75% {
      transform: scale(calc(1 - 0.2));
    }
    100% {
      left: 0;
    }
  }
